   .modal-window {
       position: fixed;
       top: 50%;
       left: 50%;
       padding: 0px 40px;
       width: 469px;
       height: 308px;
       border-radius: 20px;
       text-align: center;
       margin: auto;
       background: #E8E8E2;
       opacity: 0;

   }

   .backdrop {
       opacity: 0;

   }

   .backdrop:not(.is-hidden) .modal-window {
       transform: translate(-50%, -50%);
       opacity: 1;
   }

   .backdrop {
       position: fixed;
       top: 0;
       left: 0;
       width: 100%;
       height: 100%;
       background-color: rgba(0, 0, 0, 0.5);
       opacity: 1;
   }

   .backdrop .is-hidden {
       opacity: 0;
       pointer-events: none;
   }

   .svg-close {
       width: 28px;
       height: 28px;
       flex-shrink: 0;
       fill: #010101;

   }

   .modal-close {
       position: absolute;
       right: 24px;
       top: 24px;
       background-color: #E8E8E2;
   }

   .box-modal {
       display: inline-flex;
       flex-direction: column;
       align-items: center;
       gap: 20px;
       height: 150px;
       width: 330px;
       position: absolute;
       top: 100px;
       left: 70px;
       bottom: 100px;
   }

   .title-modal {
       color: #010101;
       text-align: center;
       font-family: "Neue Montreal";
       font-size: 24px;
       font-weight: 500;
       line-height: 24px;
       width: 245px;

   }

   .title-green {
       color: #6D8434;
       font-family: "Neue Montreal";
       font-size: 24px;
       font-weight: 500;
       line-height: 24px;
   }

   .subtitle-modal {
       color: rgba(1, 1, 1, 0.70);
       text-align: center;
       font-family: "Neue Montreal";
       font-size: 16px;
       font-weight: 400;
       line-height: 20px;
       width: 349px;
   }



       @media screen and (max-width: 480px) {
           .modal-window {
               width: 335px;
               height: 276px;
               flex-shrink: 0;

           }


           .box-modal {
               display: inline-flex;
               flex-direction: column;
               align-items: center;
               gap: 20px;
               height: 276px;
               width: 330px;
               position: absolute;
               top: 100px;
               left: 5px;
               bottom: 100px;
           }

           .title-modal {
               width: 245px;
           }

           .subtitle-modal {
               width: 248px;
           }

       }

